import {GetGroupListView,JoinGroup,ReplyTopic} from '@/api/class-center'
import KsImage from '@/components/ks-components/image/Image.vue'
import KsCollect from '@/components/ks-components/collect/collect.vue'
import KsEmpty from '@/components/ks-components/empty/empty.vue'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'
export default {
	data(){
		return{
			baseUrl:this.$store.state.SRC+"/config/app",
			loaded:false,
			groupData:{},
			isJoin:false,
			// isFocus:false,
			showReply:false,
			grouplist:[],
			getParameters:{
				boardid:0,
				type:'0',
				pageindex:1,
				pagesize:10,
			},
			topicid:0,
			remark:'',
			content:'',
			pageCount:0,
			moreStatus:'more',
			isFocus:false,
			bottomHeight:-46,
			isFocus2:false,
			bottomHeight2:-243,
			refresh:false
		}
	},
	components:{
		KsImage,
		KsCollect,
		KsEmpty,
		UniLoadMore
	},
	onLoad: function(option) {
		this.getParameters.boardid = option.boardid
		uni.showLoading({
		    title: '加载中'
		});
		this.loadPage()
	},
	onShow(){
		if(this.refresh){
			this.refresh = false
			//执行刷新操作
			this.loadPage()
			
		}
	},
	onReachBottom(e){
		this.getParameters.pageindex++
		this.moreList()
	},
	onShareAppMessage(){
		var that = this
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: that.groupData.boardname,
			path: '/pages/class-center/group/details?boardid='+that.getParameters.boardid,
	　　　　imageUrl: that.groupData.ico||this.baseUrl+'/static/images/common/default-logo.png', 
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　}
	　　};
	　　// 返回shareObj
	　　return shareObj;
	},
	methods: {
		previewImage(imgArr,src,index){
			if(src){
				// 预览图片
				uni.previewImage({
					urls: imgArr,
					current:index,
					longPressActions: {
						itemList: ['发送给朋友', '保存图片', '收藏'],
						itemColor:'#fff',
						success: function(data) {
						},
						fail: function(err) {
							console.log(err.errMsg);
						}
					},
					success:function(data){
						
					},
					fail: function(err) {
						uni.showToast({
							title: 'err.errMsg',
							icon:'none'
						});
					}
				});
			}else{
				uni.showToast({
					title: '没有找到图片',
					icon:'none'
				});
			}
			
		},
		open(url){
			uni.navigateTo({
				url:url
			})
		},
		openTopic(url){
			this.refresh=true
			if(this.groupData.Status == 1){
				
			}else{
				
			}
			if(this.groupData.Status == 0){
				uni.showToast({
					title: '你的申请正在审核',
					icon:'none'
				})
			}else if(this.groupData.Status == 2){
				uni.showToast({
					title: '你的申请已被拒绝',
					icon:'none'
				})
			}else if(this.groupData.istrue == 1){
				uni.navigateTo({
					url:url
				})
			}else if(this.groupData.Status == 1)(
				uni.navigateTo({
					url:url
				})
			)
				
			
			
		},
		setBottomHeight(e){
			this.isFocus = true
			if(this.showReply){
				this.bottomHeight = e.detail.height 
			}
		},
		setBottomHeight2(e){
			this.isFocus2 = true
			if(this.isJoin){
				this.bottomHeight2 = e.detail.height 
			}
		},
		setNoHeight(){
			this.isFocus = false
			if(this.showReply){
				this.bottomHeight = 0
			}else{
				this.bottomHeight = -46
			}
			
		},
		setNoHeight2(){
			this.isFocus2 = false
			if(this.isJoin){
				this.bottomHeight2 = 0
			}else{
				this.bottomHeight2 = -243
			}
			
		},
		closeReply(type){
			if(type=="reply"){
				this.showReply=false
				this.isFocus=false
				this.bottomHeight = -46
				this.content = ''
			}else{
				this.isJoin=false
				this.isFocus2=false
				this.bottomHeight2 = -243
				this.remark = ''
			}
			
		},
		setResult(e){
			if(e==1){
				this.loadPage()
			}
		},
		replyTopic(id){
			this.cancel()
			this.showReply = true
			this.bottomHeight = 0
			this.topicid = id
			
		},
		reply(){
			if(this.groupData.isTourist){
				if(this.content){
					uni.showLoading({
						title: '正在提交中'
					});
					ReplyTopic({
						content:this.content,
						topicid:this.topicid
					}).then(res=>{
						uni.hideLoading();
						if(res.result){
							uni.showToast({
								title: res.msg,
								duration: 2000,
								icon:'none'
							});
							this.loadPage()
						}else{
							uni.showToast({
								title: res.msg,
								duration: 2000,
								icon:'none'
							});
						}
						this.content = ''
						this.topicid = 0
						this.showReply = false
						this.isFocus = false
						this.bottomHeight = -46
					})
				}else{
					uni.showToast({
						title: '请输入回复内容',
						duration: 2000,
						icon:'none'
					});
				}
			}else{
				var that = this
				that.$store.ready(state=>{
					if(state.isLogin == 1){
						console.log(state.isLogin)
						if(that.content){
							uni.showLoading({
								title: '正在提交中'
							});
							ReplyTopic({
								content:that.content,
								topicid:that.topicid
							}).then(res=>{
								uni.hideLoading();
								if(res.result){
									uni.showToast({
										title: res.msg,
										duration: 2000,
										icon:'none'
									});
									that.loadPage()
								}else{
									uni.showToast({
										title: res.msg,
										duration: 2000,
										icon:'none'
									});
								}
								that.content = ''
								that.topicid = 0
								that.showReply = false
								that.isFocus = false
								that.bottomHeight = -46
							})
						}else{
							uni.showToast({
								title: '请输入回复内容',
								duration: 2000,
								icon:'none'
							});
						}
					}else{
						uni.showToast({
							title: '您未登录，请先登录',
							duration: 1500,
							icon:'none'
						});
						setTimeout(function () {
						    uni.navigateTo({
						    	url:'/pages/user/login/index'
						    })
						}, 1500);
						that.content = ''
						that.topicid = 0
						that.showReply = false
						that.isFocus = false
						that.bottomHeight = -46
					}
					
				})
			}
			
		},
		setTabIndex(index){
			this.getParameters.type = index
			
			this.loadPage()
		},
		moreList(){
			if(this.getParameters.pageindex > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
		},
		loadPage(status){
			if(status=='more'){
				GetGroupListView(this.getParameters).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						this.grouplist = this.grouplist.concat(res.data)
					}
					this.loaded = true
				})
			}else{
				
				this.getParameters.pageindex = 1
				GetGroupListView(this.getParameters).then(res=>{
					if(res.result){
						this.groupData = res
						this.pageCount = res.pageCount
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
						this.grouplist = res.data
					}else{
						this.grouplist = []
						this.pageCount = 0
					}
					this.loaded = true
					uni.hideLoading();
				})
			}
		},
		cancel(){
			this.isJoin=false
			this.isFocus2 = false
			this.bottomHeight2 = -243
			this.remark = ''
		},
		joinGroup(){
			this.isJoin = true
			this.bottomHeight2 = 0
			// this.isFocus = true
		},
		submit(type,item){
			this.$store.ready(state=>{
				if(state.isLogin == 1){
					if(type==1){
						if(item.isVip == 1 && item.vip_ischarge==0){
							this.submitFree()
						}else{
							this.submitNoFree()
						}
					}else{
						this.submitFree()
					}
					
				}else{
					this.refresh = true
					uni.showToast({
					    title: '您未登录，请先登录',
					    duration: 1500,
						icon:'none'
					});
					setTimeout(function () {
					    uni.navigateTo({
					    	url:'/pages/user/login/index'
					    })
					}, 1500);
					this.remark = ''
					this.isJoin = false
					this.isFocus2 = false
					this.bottomHeight2 = -243
				}
			})
			
		},
		submitFree(){
			uni.showLoading({
			    title: '正在提交中'
			});
			JoinGroup({
				boardid:this.getParameters.boardid,
				remark:this.remark
			}).then(res=>{
				uni.hideLoading();
				if(res.result){
					this.$store.dispatch('getUserInfo').then(_=>{
						uni.showToast({
						    title: res.msg,
						    duration: 2000,
							icon:'none'
						});
						this.loadPage()
					})
					
				}else{
					uni.showToast({
					    title: res.msg,
					    duration: 2000,
						icon:'none'
					});
				}
				this.remark = ''
				this.isJoin = false
				this.isFocus2 = false
				this.bottomHeight2 = -243
			})
		},
		submitNoFree(){
			var that = this
			uni.showModal({
			    title: '提示',
			    content: '您是否确定支付，确认支付后将对您的余额进行扣款？',
			    success: function (res) {
			        if (res.confirm) {
						uni.showLoading({
						    title: '正在提交中'
						});
						JoinGroup({
							boardid:that.getParameters.boardid,
							remark:that.remark
						}).then(res=>{
							uni.hideLoading();
							if(res.result){
								that.$store.dispatch('getUserInfo').then(_=>{
									uni.showToast({
									    title: res.msg,
									    duration: 2000,
										icon:'none'
									});
									setTimeout(function () {
									    that.loadPage()
									}, 2000);
									
								})
								
							}else{
								uni.showToast({
								    title: res.msg,
								    duration: 2000,
									icon:'none'
								});
							}
							that.remark = ''
							that.isJoin = false
							that.isFocus2 = false
							that.bottomHeight2 = -243
						})
			            
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		}
	}
}
